import React from "react";
import Layout from "../components/layout";

import { graphql } from 'gatsby';


export default function BlogPost({ data }) {

  const post = data.markdownRemark;

  //タグ表示
  let teg_test = "";
  if (data.markdownRemark.frontmatter.tags) {
    data.markdownRemark.frontmatter.tags.forEach(tag => {
      teg_test = teg_test + tag + ", ";
    });
    teg_test = (<span className="blog-post-top-tag">{teg_test}</span>)
  }

  return (
    <Layout>

      <div className="blog-post">
        <div className="blog-post-top">
          <span className="blog-post-top-date">
            {data.markdownRemark.frontmatter.date}
          </span>
          {teg_test}
          <h1>{data.markdownRemark.frontmatter.title}</h1>
        </div>
        <div className="blog-contents" dangerouslySetInnerHTML={{ __html: post.html }} />
      </div>
    </Layout>
  );
}

export const query = graphql`
  query($slug: String!) {
    markdownRemark( fields: { slug: { eq: $slug }}) {
      html
      frontmatter {
        date(formatString: "MMM D, YYYY")
        title
        tags
      }
    }
  }
`