import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

import HeaderMenuItem from "./header-menu-item"

import { useState, useEffect, useRef } from 'react';

//import { withPrefix } from 'gatsby'

const Header = ({ siteTitle }) => {
  const target = useRef(null);
  const [classNames, setClassNames] = useState("isScrollTop")
  useEffect(() => {
    const cb = (entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          //クラスの付け替え
          setClassNames("isScrollTop")
        } else {
          //クラスの付け替え
          setClassNames("isNotScrollTop")
        }
      })
    };

    const io = new IntersectionObserver(cb);
    io.observe(target.current)
    return () => {
      io.disconnect()
    }
  })

  return (
    <>
      <span id="ifScrollTop" ref={target} />
      <header id="global_header" className={classNames} >
        <nav id="global_navigation_bar">
          <h1>
            <Link to="/">{/*siteTitle*/}Yamanoha</Link>
          </h1>
          <ul className="parent-menu-item">
            <HeaderMenuItem to="/">about</HeaderMenuItem>
            <HeaderMenuItem to="/prodact">prodact</HeaderMenuItem>
            <HeaderMenuItem to="/blog">blog</HeaderMenuItem>
          </ul>
        </nav>
      </header>
      <div id="global_navigation_menu_button"  className={classNames}>
        <img src="/icon/bars_24.svg" alt=""/>
      </div>
    </>
  )
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header